footer {
  background-color: rgb(255, 255, 255);
  padding: 15px 0px 10px;
}

.footer-container {
  max-width: 1270px;
  padding-left: 15px;
  padding-right: 15px;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;

  h5 {
    font-size: 12px;
    font-weight: 600;
    margin: 0px 0px 5px;
  }

  p {
    font-size: 11px;
    font-weight: 300;
    margin: 0px;
  }
  .block {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    width: 226px;
  }
  a {
    margin-right: 4px;
    text-decoration: none;
    color: rgb(0, 127, 240);
    img {
      max-width: 100%;
      }
  }
}

@media only screen and (min-width: 1200px) {
  .footer-container {
    flex-direction: row;
  }
}