.options-wrapper {
  padding: 47px 20px;

  .title {
    text-align: center;
    font-size: 36px;
    margin-bottom: 47px;
  }

  .blocks {
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    flex-wrap: wrap;

    .block {
      max-width: 100%;
      width: 320px;
      box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 14px 0px;
      background-color: rgb(255, 255, 255);
      margin: 20px 12px;
      border-radius: 10px;
      padding: 20px;

      .block-image {
        height: 186px;
        display: flex;
        margin-bottom: 20px;
        -webkit-box-pack: center;
        justify-content: center;

        img {
          align-self: center;
        }
      }

      .block-title {
        font-size: 24px;
        margin-bottom: 16px;
        color: rgb(46, 46, 46);
        font-weight: 300;
        text-align: center;
      }
  
      .block-content {
        color: rgb(52, 52, 52);
        font-weight: 300;
        font-size: 16px;
        margin-bottom: 24px;
        height: 74px;
        text-align: center;
      }

      .block-link {
        text-decoration: none;
        font-size: 16px;
        font-weight: 500;
        color: rgb(59, 137, 235);
        line-height: 40px;
        text-align: center;
        display: inline-block;
        width: 100%;
        cursor: pointer;
        background-color: rgba(29, 111, 218, 0.1);
        border-radius: 4px;
      }
    }
  }
}