*{margin:0;padding:0;box-sizing:border-box;font-family:Helvetica, Arial, sans-serif}.intro *{font-family:'Nunito Sans', sans-serif}.intro .mobile-header{position:fixed;z-index:10;top:0px;left:0px;right:0px;background-color:#1ba8ff;display:flex;align-items:center;height:56px;padding:0px 8px}.intro .mobile-header .btn-back{min-width:40px;height:40px;color:#fff;display:flex;justify-content:center;align-items:center;padding:0px;border-width:0px;border-style:none;border-style:initial;border-color:currentColor;border-color:initial;border-image:none;border-image:initial;background:0px center;outline:0px}.intro .mobile-header svg{stroke:currentcolor;fill:currentcolor;stroke-width:0;height:30;width:30}.intro .mobile-header__title{color:#fff;font-size:17px;white-space:nowrap;text-overflow:ellipsis;text-align:center;padding-right:40px;overflow:hidden;flex:1 1}.intro header{width:100%;top:0}.intro header nav{margin:auto;height:80px}.intro header ul{display:none}.intro nav ul a{text-decoration:none;color:black}.intro a[role="button"]{display:inline-block;text-align:center;text-decoration:none;font-weight:700}.intro nav ul li:last-child a{background:url("/assets/images/Group_17.svg") no-repeat bottom;width:130px;height:40px;line-height:40px;color:white;background-size:cover}.intro .container{background:url("/assets/images/Group_23.svg") no-repeat bottom,url("/assets/images/Path_4.svg") no-repeat 0 0;background-size:cover, 180px 132px;min-height:790px;margin-top:-80px;display:flex;align-items:center;justify-content:center}.intro .wrapper{margin-top:80px;width:90%;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center}.intro .left,.intro .right{width:100%}.intro img[src="./assets/images/logo.png"]{z-index:1;margin-bottom:50px}.intro .left h1{line-height:3rem;margin-bottom:10px;font-weight:800}.intro .left a[role="button"]{width:171px;height:44px;border:2px solid #039ab4;border-radius:50px;line-height:44px;margin:50px auto 50px;font-size:.8rem;color:#039ab4}.intro .right img{max-width:100%;-webkit-animation-name:fly;animation-name:fly;-webkit-animation-duration:2s;animation-duration:2s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-direction:alternate;animation-direction:alternate}@-webkit-keyframes fly{0%{transform:translateY(0)}100%{transform:translateY(10px)}}@keyframes fly{0%{transform:translateY(0)}100%{transform:translateY(10px)}}@media only screen and (min-width: 1200px){.intro .mobile-header{display:none}.intro header nav{width:1000px}.intro header ul{list-style-type:none;display:flex;height:80px;justify-content:space-between;align-items:center;margin-left:auto;width:70%;font-weight:700;font-size:.8rem}.intro .container{height:100vh;max-height:1080px}.intro .wrapper{flex-direction:row;width:1110px;display:flex;text-align:left}.intro .left a[role="button"]{margin:50px auto 100px}}

.options-wrapper{padding:47px 20px}.options-wrapper .title{text-align:center;font-size:36px;margin-bottom:47px}.options-wrapper .blocks{display:flex;justify-content:center;flex-wrap:wrap}.options-wrapper .blocks .block{max-width:100%;width:320px;box-shadow:rgba(0,0,0,0.1) 0px 0px 14px 0px;background-color:#fff;margin:20px 12px;border-radius:10px;padding:20px}.options-wrapper .blocks .block .block-image{height:186px;display:flex;margin-bottom:20px;justify-content:center}.options-wrapper .blocks .block .block-image img{align-self:center}.options-wrapper .blocks .block .block-title{font-size:24px;margin-bottom:16px;color:#2e2e2e;font-weight:300;text-align:center}.options-wrapper .blocks .block .block-content{color:#343434;font-weight:300;font-size:16px;margin-bottom:24px;height:74px;text-align:center}.options-wrapper .blocks .block .block-link{text-decoration:none;font-size:16px;font-weight:500;color:#3b89eb;line-height:40px;text-align:center;display:inline-block;width:100%;cursor:pointer;background-color:rgba(29,111,218,0.1);border-radius:4px}

html{scroll-behavior:smooth}

.product .options-wrapper{background-color:#fcfcfc}.product .blocks{max-width:100%;width:1000px;margin:auto}.product .blocks .block{width:100%}

.options-wrapper .blocks .block{margin:10px 12px}.options-wrapper .blocks .block .block-content{height:-webkit-max-content;height:-moz-max-content;height:max-content}

.item{cursor:pointer;display:inline-block;margin:5px;padding:5px 10px;border:1px solid lightgray;border-radius:4px;transition:all .3s ease-in}.item.active{background-color:rgba(29,111,218,0.1);color:#3b89eb;border-color:transparent}

.input-container{width:100%;display:flex;position:relative;box-shadow:rgba(98,98,98,0.5) 0px 1px 2px 0px;height:40px;margin:5px 0 20px 0}.input-container .keyword-input{font-size:13px;border-width:0px;border-style:none;border-style:initial;border-color:currentColor;border-color:initial;border-image:none;border-image:initial;padding:0px 8px;border-radius:4px 0px 0px 4px;flex:1 1;outline:none}.input-container .add{opacity:.8;cursor:pointer;margin:5px 5px 0 0;width:30px;height:30px;background:transparent;border:transparent;background-image:url("/assets/icons/add.png");background-size:contain}@media only screen and (min-width: 1200px){.input-container{width:60%;margin:5px auto 20px auto}}

.appearance .options-wrapper{background-color:#f9f9f9}.appearance .blocks{max-width:100%;width:1000px;margin:auto}.appearance .blocks .block{width:100%}

footer{background-color:#fff;padding:15px 0px 10px}.footer-container{max-width:1270px;padding-left:15px;padding-right:15px;margin-right:auto;margin-left:auto;display:flex;justify-content:space-between;align-items:center;flex-direction:column}.footer-container h5{font-size:12px;font-weight:600;margin:0px 0px 5px}.footer-container p{font-size:11px;font-weight:300;margin:0px}.footer-container .block{display:flex;align-items:center;width:226px}.footer-container a{margin-right:4px;text-decoration:none;color:#007ff0}.footer-container a img{max-width:100%}@media only screen and (min-width: 1200px){.footer-container{flex-direction:row}}

