@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;700;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Helvetica, Arial, sans-serif;
}

.intro {
  
  & * {
    font-family: 'Nunito Sans', sans-serif;
  } 
  
  .mobile-header {
    position: fixed;
    z-index: 10;
    top: 0px;
    left: 0px;
    right: 0px;
    background-color: rgb(27, 168, 255);
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    height: 56px;
    padding: 0px 8px;

    .btn-back {
        min-width: 40px;
        height: 40px;
        color: rgb(255, 255, 255);
        display: flex;
        -webkit-box-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        align-items: center;
        padding: 0px;
        border-width: 0px;
        border-style: initial;
        border-color: initial;
        border-image: initial;
        background: 0px center;
        outline: 0px;
    }

    svg {
        stroke: currentcolor;
        fill: currentcolor;
        stroke-width: 0;
        height: 30;
        width: 30;
    }

    &__title {
        color: rgb(255, 255, 255);
        font-size: 17px;
        white-space: nowrap;
        text-overflow: ellipsis;
        text-align: center;
        padding-right: 40px;
        overflow: hidden;
        flex: 1 1 0%;
    }
  }

  header {
    width: 100%;
    top: 0;
  }
  
  header nav {
    margin: auto;
    height: 80px;
  }
  
  header ul {
    display: none;
  }
  
  nav ul a {
    text-decoration: none;
    color: black;
  }
  
  a[role="button"] {
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-weight: 700;
  }
  
  nav ul li:last-child a {
    background: url('/assets/images/Group_17.svg') no-repeat bottom;
    width: 130px;
    height: 40px;
    line-height: 40px;
    color: white;
    background-size: cover;
  }
  
  .container {
    background: url('/assets/images/Group_23.svg') no-repeat bottom, url('/assets/images/Path_4.svg') no-repeat 0 0;
    background-size: cover, 180px 132px;
    min-height: 790px;
    margin-top: -80px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .wrapper {
    margin-top: 80px;
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  .left, .right {
    width: 100%;
  }
  
  img[src="./assets/images/logo.png"] {
    z-index: 1;
    margin-bottom: 50px;
  }
  
  .left h1 {
    line-height: 3rem;
    margin-bottom: 10px;
    font-weight: 800;
  }
  
  .left a[role="button"] {
    width: 171px;
    height: 44px;
    border: 2px solid rgb(3, 154, 180);
    border-radius: 50px;
    line-height: 44px;
    margin: 50px auto 50px;
    font-size: .8rem;
    color: rgb(3, 154, 180);
  }
  
  .right img {
    max-width: 100%;
    animation-name: fly;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
  }
  
  @keyframes fly {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(10px);
    }
  }
  
  @media only screen and (min-width: 1200px) {
    .mobile-header {
    display: none;
    }

    header nav {
        width: 1000px;
    }
    
    header ul {
        list-style-type: none;
        display: flex;
        height: 80px;
        justify-content: space-between;
        align-items: center;
        margin-left: auto;
        width: 70%;
        font-weight: 700;
        font-size: .8rem;
    }

    .container {
        height: 100vh;
        max-height: 1080px;
    }

    .wrapper {
        flex-direction: row;
        width: 1110px;
        display: flex;
        text-align: left;
    }

    .left a[role="button"] {
        margin: 50px auto 100px;
    }
  }
}